import React from 'react';
import Wave from './Wave';
import styled from 'styled-components';
import bg from '../img/bg.svg';
import { FaArrowCircleDown } from 'react-icons/fa'

const Container = styled.div`
    width: 100%;
    height: 100vh;
`

const Contenido = styled.div`
    height: 60vh;
    background: #c7ffd8;
    display: flex;
    align-items: center;
    justify-content: space-evenly;

`
const Bg = styled.img`
    width: 150px;
`
const Texto = styled.p`
    font-size: 24px;
    font-weight: 800;
    color: #0ba288;
    padding: 10px;
`

const ContainerArrow = styled.div`
    width: auto;
    height: auto;
    position: absolute;
    bottom: 30%;
`

const Link = styled.a`
    text-decoration: none;
    color: black;
`

const Home = () => {
    return ( 
        <Container>
            <Contenido>
                <Texto>INNOVACIÓN, DEDICACIÓN Y EVOLUCIÓN, CON NOSOTROS</Texto>
                <Bg src={bg} />
                <ContainerArrow>
                    <p>MÁS INFORMACIÓN</p>
                    <Link href="#services"><FaArrowCircleDown className="arrowDown" /></Link>
                </ContainerArrow>
            </Contenido>
            <Wave />
        </Container>
    );
}
 
export default Home;